export default {
	install(Vue) {
		Vue.mixin({
			data() {
				return {
					"userRules": {
						"loginName": {
							"rules": [{
								"required": true,
								"errorMessage": ""
							},
							{
								validateFunction:this.validateLoginName   // 
							},
							]
						},
						"password":{
							"rules":[
								{
									"required":true,
									"errorMessage":""
								}
							]
						},
						"phone":{
							"rules":[
								{
									"required":true,
									"errorMessage":""
								},
								{
									validateFunction:this.validatePhone // 
								}
							]
						},
						"vCode":{
							"rules":[
								{
									"required":true,
									"errorMessage":""
								},{
									validateFunction:this.validateCode // 
								}
							]
						}
					}
				}
			},
			methods: {
				// 
				validateLoginName(rule,val,data,callback){
					switch(true){
						case val.length < 6 :
						callback('')
						break;
						default :
						callback();
					}
				},
				// 
				validatePhone(rule,val,data,callback){
					const reg = /^1[\d]{10,10}$|^[\d]{8,8}$/
					switch(true){
						case !reg.test(val):
						callback("");
						break;
						default:
						callback()
					}
				},
				// 
				validateCode(rule,val,data,callback){
					switch(true){
						case val !== this.returnCode:
						callback("");
						break;
						default:
						callback()
					}
				}
			}
		})
	}
}
