//  --- 
// http
// promise
export default ({
	name, // 
	data = {} // 
}) => {
	return new Promise((resolve,reject)=>{
		!data.isLoading && uni.showLoading({}) //
		data.isLoading && delete data.isLoading; // isLoading
		uniCloud.callFunction({ // uniCloud
			name, // 
			data, // 
			success({result}){
				const {code,data,msg} = result; // code data msg
				if(code === 0){  // 
					resolve(data) 
				}else{ // 
					uni.showToast({
						icon:"none",
						title:msg
					})
				}
			},// 
			fail(err){ // 
				reject(err)
			},// 
			complete(){ // 
				!data.isLoading && uni.hideLoading() // 
			}//
		})
	})
}
